(function(){
	'use strict';
	
/*--------globall helper function that creates nav menus ------*/
	
	window.createSubMenu = function(model, menu, location){
			model.forEach(function(element){
			if(element){
				var listItem = document.createElement('li');
				listItem.dataset.id = Object.getOwnPropertyNames(element)[0];
				var textNode = document.createTextNode(element.item || element.subItem);
				var link = document.createElement('a');
				link.appendChild(textNode);

				if(element.subItems) {
					var arrow = document.createElement('span');
					arrow.classList.add('fontawesome-angle-down');
					arrow.dataset.text = 'angle-down';
					link.appendChild(arrow);
					var subMenu = document.createElement('ul');
					subMenu.classList.add('sub-menu');			
					window.createSubMenu(element.subItems, subMenu, listItem);
				}

				listItem.appendChild(link);
				if(subMenu) {
					listItem.appendChild(subMenu);
				}
				menu.appendChild(listItem);
				location.appendChild(menu);
				}
			})
		};
		
/*-------------------------End----------------------------*/
	
/*---------------globall AJAX request function--------------*/	
	
	window.Ajax = function(obj){
		var method = obj.method,
				path = obj.path,
				success = obj.success;
		
		var req = new XMLHttpRequest();
      req.open(method, path);
      req.onreadystatechange = function(aEvt) {
      	if (req.readyState === 4) {
        	if (req.status === 200) {
							var response;
							try {
								response = JSON.parse(req.responseText);
							} catch (e) {
								console.error(e);
							}
					success(response);
        } else {	
            error(req, aEvt);  
					}
				}
			};
         req.send();
	};
	/*-----------------------End-----------------------------*/
})();
(function(){
	"use strict";
	
var hamburgerBtn = document.querySelector('.toggle-menu'),
		searchBtn = document.querySelector('.search'),
		toggleMenu = document.querySelector('.header-menubar'),
		buttonsList = document.querySelector('.buttons-list'),
		header = document.querySelector('.header-inner'),
		subMenu,
		currentBtn,
		prevBtn,
		toggleMenuModel;
	
/*---Loading header model, creating and appendin menu to the page when loaded-----*/
	
window.addEventListener('load', function(){
	
	var mainList = document.createElement('ul');
			mainList.classList.add('menu-list');
	
		Ajax({
					method: "GET",
					path: "models/headerModel.json",
					success: function(result) {
					toggleMenuModel = result;
					createSubMenu(toggleMenuModel, mainList, toggleMenu);	
					}
		});
	
	});
	
/*-----------------End------------------*/	

	
/*---------------Controls different menu states-----------------------*/	
/*----------almighty detoggler will sweep you away, be aware!---------*/
	
function deToggler(currentTrgt){
	var currentActiveTggl = header.querySelector('.active'),
			currentActiveForm = header.querySelector('.active-form'),
			highLightedBtn = header.querySelector('.highlight'),
			hasDataset = currentBtn.dataset.id,
			currentSlideMenuList;
	
	
	if(prevBtn){
		var	prevTrgt = prevBtn.querySelector('a');
	}
	
	/* checks something else ??? */
	
	function checkListItem(){
		if(currentBtn.dataset.id === 'item' && prevBtn) {
			if (prevBtn.dataset.id  && (currentTrgt.textContent !== prevTrgt.textContent)) {
				if(highLightedBtn){
						highLightedBtn.classList.remove('highlight');
				}
				
				currentSlideMenuList = header.querySelectorAll('.slide-down');
				
						if(currentSlideMenuList.length > 0) {
							Array.prototype.forEach.call(currentSlideMenuList, function(element){
								element.classList.remove('slide-down');
							});
							if(prevBtn.dataset.id === 'subItem'){
								var currentSubmenu = currentBtn.querySelector('.sub-menu');
								currentSubmenu.classList.add('slide-down');
							}
						}
					}
			}
	};
	
	
	/* checks if different buttons where pressed while showing toggle-menu on medium & small screens */
	
	if(prevBtn && currentActiveTggl){
		checkListItem();
		if (!hasDataset){
			currentActiveTggl.classList.remove('active');
		}
	}
	
	/* checks if toggle button was pressed */
	
	if (currentBtn === hamburgerBtn){
		if(currentActiveTggl) {
			currentActiveTggl.classList.toggle('active');
			currentBtn.classList.toggle('highlight');
		}
	}
	
	/* checks if the same level menu items where pressed when displayed on wide screen */
	
	if(hasDataset) {
		checkListItem();
	}
		
	/* checks if search-form was in interaction */
	
	if (currentActiveForm) {
		if(prevBtn) {
				if(document.activeElement.parentNode === currentActiveForm){
					return false;
			} else {
				prevBtn.classList.remove('highlight');
				currentActiveForm.classList.remove('active-form');
			}
		}
	}
};
/*-----------------End--------------------*/
	
/*----------Listening to click events and interacting with menu-------*/	
	
	header.addEventListener('click', function(e){
	var target = e.target;	
	currentBtn = e.target.parentNode;
		
			/* Checks if clicked outside menu */	
		
	function bodyListener(e) {
		if(e.target.parentNode.dataset.id) {
			return false;
	} else {
			var currentSlideMenuList = header.querySelectorAll('.slide-down'),
			highLightedBtn = header.querySelector('.highlight');
		
			if(currentSlideMenuList.length > 0) {
				Array.prototype.forEach.call(currentSlideMenuList, function(element){
						element.classList.remove('slide-down');
				});
			}
			if(highLightedBtn) {
				highLightedBtn.classList.remove('highlight');
			}
		
		document.body.removeEventListener('click', bodyListener);
		}
	};
		
	document.body.addEventListener('click', bodyListener);
		
		/* checks if current button if different from previous */
		
if (prevBtn !== currentBtn) {
		deToggler(target);
	};
		
	var menuList = document.querySelector('.menu-list'),	
			parent = e.target.parentNode,
			subMenu = parent.querySelector('.sub-menu');
		
		/* checks if menu item was pressed */
		
	if(e.target.parentNode.dataset.id) {
		if (e.target.parentNode.dataset.id === 'item'){
			e.target.parentNode.classList.toggle('highlight');
		}
			if(subMenu){
				subMenu.classList.toggle('slide-down');
			}
	}
		
		/* checks if search button was pressed */
		
	if(e.target.dataset.text === 'search') {
			var searchForm = document.searchForm;
			setTimeout(function(){
				searchForm.classList.toggle('active-form');
				e.target.parentNode.classList.toggle('highlight');
			}, 300);
	}
		
		/* checks if toggle menu button was pressed */
		
	if (e.target.dataset.text === 'reorder') {
			toggleMenu.classList.toggle('active');
			currentBtn.classList.toggle('highlight');
	}
		

	prevBtn = currentBtn;
	
	});

/*--------------------End--------------------------*/	
	
})();
(function(){
	'use strict';
	
	var signInForm = document.invForm,
			userNameInput = signInForm.userName,
			userEmailInput = signInForm.userEmail,
			userPassInput = signInForm.userPass,
			submitBtn = signInForm.formSubmit,
			required = signInForm.querySelectorAll("[required]"),
			flag;
	
	signInForm.addEventListener('submit', function(e){
		
		if(userEmailInput.value){
				if (!/\S+@\S+\.\S+/.test(userEmailInput.value)){
					console.log('Field email is not correct format');
					flag = true;
				}
		}
		
			Array.prototype.forEach.call(required, function(element){
				if(!element.value) {
					var placeholder = element.getAttribute('placeholder');
					console.log('Required Field! ' + element.name);
					element.placeholder = 'Required field!';
					element.classList.add('required');
				} else {
					element.classList.add('success');
				}
			});
		
		if(flag){
			userEmailInput.classList.remove('success');
			userEmailInput.classList.add('required');
			userEmailInput.value = '';
			userEmailInput.placeholder = 'Wrong email format!';
			flag = false;
		}
	
	
	e.preventDefault();
	return false;	
	});
	
	signInForm.addEventListener('keypress', function(e){
		e.target.classList.remove('required');
		e.target.classList.remove('success');
	});
		
})();
(function(){
	'use strict';
	
	var appleFooter = document.querySelector('footer.apple'),
			footerMenu = document.querySelector('.footer-menu'),
			menuItems,
			appleFooterModel;
	
	/*---------------Set click event listenets to top menu items-----------*/
	function doAppleStuff(){
		var falseClassList = document.querySelectorAll('.footer-list .sub-menu');
	
		Array.prototype.forEach.call(falseClassList, function(item){
			item.classList.remove('sub-menu');
			item.classList.add('footer-sub-items');
		});
		
		menuItems = footerMenu.querySelectorAll('[data-id="item"]');
		
		function onFire(item){
			item.addEventListener('click', function(){
				var prevBtn = footerMenu.querySelector('.on-fire');
				if(prevBtn && prevBtn.innerHTML !== item.innerHTML){
					prevBtn.classList.remove('on-fire');
				}
				item.classList.toggle('on-fire');
			});
		} 
	
		Array.prototype.forEach.call(menuItems, onFire);
	};
	
	/*--------Loading footer model and appending footer menu to document---------*/
	
	window.addEventListener('load', function(){
		
		var footerList = document.createElement('ul');
		footerList.classList.add('footer-list');
		
		Ajax({
					method: "GET",
					path: "models/footerModel.json",
					success: function(result) {
					appleFooterModel = result;
					createSubMenu(appleFooterModel, footerList, footerMenu);
					doAppleStuff();	
					}
		});
		
	});
	/*------------------End-------------------*/
	
})();